@import "https://fonts.googleapis.com/css?family=Montserrat:400,700|Roboto+Slab:400,700";
@import "https://fonts.googleapis.com/css?family=Concert+One|Righteous";

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html,
body,
header,
footer,
main,
section,
article,
aside,
figure,
figcaption,
nav,
div,
span,
p,
strong,
em,
ul,
li,
a,
img,
h1,
h2,
h3,
h4,
h5,
form,
button,
input {
  margin: 0;
  padding: 0; }

html,
body,
#root {
  min-width: 320px;
  min-height: 100%;
  height: auto;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

